@import '../../styles/utils';

.news_section {
  position: relative;

  min-width: 300px;

  @include onDesktop {
    width: 100%;
    max-height: 400px;
    height: 100%;

    display: flex;

    align-items: center;
  }

  &__loader {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__main {
    min-height: 200px;
    height: 100%;
    flex-basis: 60%;
    position: relative;

    &_title {
      font-size: 14px;

      @include onTablet {
        font-size: 25px;
        line-height: 30px;
        font-weight: 800;
      }

      cursor: pointer;

      position: absolute;
      padding: 15px;
      bottom: 0;

      text-decoration-color: $main-green;

      &:hover {
        text-decoration: underline;
        text-decoration-color: $main-green;
      }
    }

    &_image {
      width: 100%;
      border-radius: $border-radius-small;
      height: 100%;

      object-fit: cover;

      cursor: pointer;
      text-decoration-color: $main-green;

      transition-duration: 500ms;

      &:hover + .news_section__main_title {
        text-decoration: underline;
        text-decoration-color: $main-green;
      }
    }
  }

  &__secondary {
    min-height: 200px;
    flex-basis: 40%;
    height: 100%;

    display: flex;
    flex-wrap: wrap;

    &_container {
      box-sizing: border-box;

      height: 50%;
      width: 50%;
      position: relative;
      cursor: pointer;
      padding-top: 10px;

      &:nth-child(1),
      &:nth-child(3) {
        padding-right: 10px;
      }

      @include onDesktop {
        padding-left: 10px;
        padding-top: 0;

        &:nth-child(1),
        &:nth-child(2) {
          padding-bottom: 10px;
        }

        &:nth-child(1),
        &:nth-child(3) {
          padding-right: 0;
        }
      }
    }

    &_image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: $border-radius-small;

      text-decoration-color: $main-green;

      &:hover + .news_section__secondary_title {
        text-decoration: underline;
        text-decoration-color: $main-green;
      }
    }

    &_title {
      font-size: 12px;

      @include onTablet {
        font-size: 18px;
      }

      @include onDesktop {
        font-size: 14px;
      }

      text-decoration-color: $main-green;

      position: absolute;
      bottom: 0;
      padding: 10px;

      &:hover {
        text-decoration: underline;
        text-decoration-color: $main-green;
      }
    }
  }
}
